import React, { useEffect, useState } from "react";
import axios from 'axios';
import queryString from "query-string";
import { useLocation } from 'react-router-dom';
import { SERVER_API } from "config";
import { toast } from 'react-toastify';

import PageHeader from "components/PageHeader";
import ProductsList from 'components/ProductsList';
import Categories from './Categories';

import './style.scss';
export default function ShopPage() {
    const client = axios.create({ baseURL: SERVER_API });
    const location = useLocation();
    const params = queryString.parse(location.search);

    const [searchValue, setSearchValue] = useState('');
    const [products, setProducts] = useState([]);
    const [onload, setOnload] = useState(true);
    const [filteredProducts, setFilteredProducts] = useState([null, null, null, null, null, null]);

    const getData = () => {
        client.get('/products').then((rs) => {
            setOnload(false);
            setProducts(s => rs.data.data);
            setFilteredProducts(s => rs.data.data);
        }).catch(e => {
            if (e.response && e.response.data) {
                toast.error(e.response.data.errors[0]);
            } else {
                toast.error('Kết nối máy chủ thất bại, vui lòng thử lại');
            }
        });
    }
    useEffect(() => { getData(); }, []);

    useEffect(() => {
        if (params.filter && params.filter !== 'all' && products[0]) {
            setFilteredProducts(s => {
                return products.filter(x => x.category.toLowerCase() === params.filter.toLocaleLowerCase());
            })
        } else {
            setFilteredProducts(s => {
                return products;
            });
        }
        setSearchValue('');
    }, [params.filter, products.length])

    const filterByKey = (e) => {
        setSearchValue(e.target.value.toLowerCase());
        setFilteredProducts(s => {
            return products.filter(x => x.name.toLowerCase().includes(e.target.value.toLowerCase()));
        })
    }

    return (
        <main>
            <section className="clear--padding">
                <div className="container">
                    <PageHeader title='Shop' slug='' last='shop' />
                </div>
            </section>
            <section>
                <div className="container">
                    <div className="flex flex--gap">
                        <div className="col-sm-12 col-lg-3">
                            <Categories />
                        </div>
                        <div className="col-sm-12 col-lg-9">
                            <div>
                                <input type='text' className="input" value={searchValue} onChange={filterByKey} placeholder="Tìm kiếm theo tên.." />
                            </div>
                            <div className="gap"></div>
                            <ProductsList items={filteredProducts} onload={onload} column={3} preview={false} />
                        </div>
                    </div>

                </div>
            </section>

        </main>
    )
}